    <Sheet
      v-model="jiraIssueSheet"
      width="40vw"
      has-footer
      @input="closeJiraIssueSheet"
    >
      <!-- title -->

      <template #title> JIRA - Create Issue </template>

      <!-- ... -->

      <template #default>
        <div id="content-wrapper" class="jiraIssue">
          <ValidationObserver ref="form">
            <div class="field-wrapper">
              <ValidationProvider
                v-slot="{ errors }"
                name="Issue Name"
                :rules="{ required: true }"
              >
                <TextField
                  v-model="issueName"
                  label="Issue Name"
                  is-mandatory
                  :error="errors[0]"
                  class="field"
                />
              </ValidationProvider>

              <ValidationProvider
                v-if="false"
                v-slot="{ errors }"
                name="Project Key"
                :rules="{ required: true }"
              >
                <TextField
                  v-model="projectKey"
                  label="Project Key"
                  is-mandatory
                  :error="errors[0]"
                  tooltip="Unique identifier key of your Project"
                  class="field"
                />
              </ValidationProvider>

              <ValidationProvider
                v-if="false"
                v-slot="{ errors }"
                name="Issue Type"
                :rules="{ required: true }"
              >
                <TextField
                  v-model="issueType"
                  label="Issue Type"
                  is-mandatory
                  :error="errors[0]"
                  class="field"
                />
              </ValidationProvider>

              <ValidationProvider
                v-slot="{ errors }"
                name="Description"
                :rules="{ required: true }"
              >
                <TextAreaField
                  v-model="issueDescription"
                  label="Description"
                  is-mandatory
                  :error="errors[0]"
                  class="field"
                />
              </ValidationProvider>

              <!-- <ValidationProvider
                v-slot="{ errors }"
                name="Epic Value"
                :rules="{ required: true }"
              >
                <TextField
                  v-model="issueEpicValue"
                  label="Epic Value"
                  is-mandatory
                  :error="errors[0]"
                  class="field"
                />
              </ValidationProvider> -->

              <CheckboxField
                v-if="false"
                v-model="issueDescriptionTable"
                label="Include inbox request information as a table of issue description"
                class="field"
              />
              <!-- v-if="issueDescriptionTable" -->
              <MultiSelectField
                v-if="false"
                v-model="issueDescriptionFields"
                label="Request Fields"
                :is-searchable="true"
                :options="formFields"
                class="field"
              />

              <SelectField
                v-if="false"
                v-model="issueAssignee"
                label="Assignee"
                :is-searchable="true"
                :options="issueAssigneeUsers"
                class="field"
              />
            </div>
          </ValidationObserver>
        </div>
      </template>

      <template #footer>
        <BaseButton
          label="Create Issue"
          color="primary"
          @click="createJiraIssue"
        />
      </template>
    </Sheet>

    <MetaDataEdit
      :value="showMetaDataEditModal"
      :file="workflow"
      :repository-field="repositoryField"
      :file-data="fileData"
      :title="metaDataTitle || 'Index'"
      :columns="columns"
      :modal-value="workflowModel"
      :read-only="true"
      :upload-filename="uploadFilename"
      :meta-data-fields="metaDataModal"
      @close="closeMetaDataEditModal"
      @save="saveMetaData"
    />

    <Sheet v-model="taskSheet" width="30vw" @input="closeTaskSheet">
      <!-- title -->

      <template #title> Tasks </template>

      <!-- ... -->

      <template #actions>
        <BaseActionButton
          v-tooltip.top="'refresh'"
          icon="mdi-refresh"
          color="secondary"
          no-border
          is-flat
          size="20px"
        />

        <BaseActionButton
          v-tooltip.top="'Add Task'"
          icon="mdi-plus"
          color="secondary"
          no-border
          is-flat
          size="20px"
          class="q-mr-sm"
          @click="addTask()"
        />
      </template>

      <!-- content -->
      <div id="content-wrapper">
        <template v-for="task in taskEntries">
          <div :key="task.id" class="taskEntries">
            <div class="col-12">
              <div class="row">
                <div
                  class="col-auto q-mr-md"
                  style="margin-top: 3px"
                  @click="viewTask(task.id)"
                >
                  {{ task.name }}
                </div>
                <q-chip
                  size="10px"
                  :color="getColor(task.priority)"
                  text-color="white"
                >
                  {{ task.priority }}
                </q-chip>
                <q-chip
                  size="10px"
                  :color="getColor(task.status)"
                  text-color="white"
                >
                  {{ task.status }}
                </q-chip>
              </div>
            </div>

            <div class="meta row">
              <div class="col-12">Assigned To: {{ task.assigned }}</div>
              <div class="col-12">
                Start Date: {{ $day.format(task.startDate, false) }}
                <BaseIcon name="mdi-circle-small" />
                End Date: {{ $day.format(task.endDate, false) }}
              </div>
            </div>
          </div>
        </template>
      </div>
    </Sheet>

    <AddTask
      v-if="taskFormId"
      :value="addTaskSheet"
      :form-settings="addTaskForm.settings"
      :panels="addTaskForm.panels"
      :secondary-panels="addTaskForm.secondaryPanels"
      :form-id="taskFormId"
      :repository-id="taskRepositoryId"
      :task-model="selectedTask"
      :workflow-task="true"
      :mode="taskMode"
      @close="addTaskSheet = false"
      @addEntry="addTaskEntry"
    ></AddTask>

    <ImportPopup
      v-if="importExcel"
      :chip-button="true"
      :data-imported="dataImported"
      :form-id="String(masterFormId)"
      class="row items-center q-mr-xs"
      @upload="$refs.input.click()"
      @close="dataImported = false"
    />

    <input
      ref="input"
      type="file"
      accept=".csv,.xls,.xlsx"
      class="hidden"
      @change="uploadFile"
    />

    <Confirm
      v-model="alertModal"
      icon="mdi-file-alert"
      icon-color="red"
      @input="closeAlertModal"
    >
      <template #title>Documents Required!</template>

      <template #default>
        <div class="q-py-md text-md">
          <p>
            Are you sure you want to submit the request without attaching the
            required documents.
          </p>

          <!-- <p class="text-secondary">
            After submitting, you can upload the required documents from sent
            list once you have them.
          </p> -->
        </div>
      </template>

      <template #footer>
        <BaseButton
          is-flat
          label="no"
          class="q-mr-sm"
          @click="closeAlertModal"
        />
        <BaseButton label="yes" color="primary" @click="proceedSubmission" />
      </template>
    </Confirm>

    <EmailSharing
      v-model="emailSharingSheet"
      :repository-id="workflow.repositoryId"
      :file-list.sync="selectedFileList"
    ></EmailSharing>

    <FileDetails v-model="fileDetailsSheet" :file-data="fileData"></FileDetails>

    <ProcessHistory
      v-if="historyProcessId"
      :process-id="historyProcessId"
      :selected-process="selectedProcess"
      @close="historyProcessId = 0"
    />

    <FormEntryHistory
      v-if="showFormEntryHistoryModal"
      :value.sync="showFormEntryHistoryModal"
      :workflow-id="workflow.id"
      :process-id="selectedProcess.processId"
    />

    <Attachments
      v-if="showAttachmentSheet"
      :value.sync="showAttachmentSheet"
      :selected-process="selectedProcess"
      :workflow="workflow"
      :selected-node="selectedNode"
      :initiate-process="initiateProcess"
      :form-check-list="formCheckList"
