          icon: "mdi-printer",
          count: 0,
        },
        {
          id: this.$nano.id(),
          label: "task",
          value: "TASK",
          icon: "eva-cube-outline",
          count: 0,
        },
        {
          id: this.$nano.id(),
          label: "Form Entry History",
          value: "FORM_ENTRY_HISTORY",
          icon: "mdi-file-document",
          count: 0,
        },
      ],
      taskEntries: [],
      addTaskEnabled: false,
      mailTab: "OUTGOING",
      mailTransactionSheet: false,
      commentText: "",
      mailTransactionList: [],
      mailAttachmentSheet: false,
      mailAttachmentList: [],
      fileSheet: false,
      selectedFile: {},
      mailTabs: [
        {
          id: this.$nano.id(),
          label: "outgoing",
          value: "OUTGOING",
          count: 0,
        },
        {
          id: this.$nano.id(),
          label: "incoming",
          value: "INCOMING",
          count: 0,
        },
      ],
      userList: [],
      groupList: [],
      refreshTree: true,
      loadInitial: true,
      customReply: false,
      userReply: false,
      toRequester: false,
      customCloseProcessSheet: false,
      approvalTypes: [
        {
          id: this.$nano.id(),
          label: "Approve",
          value: "Approved",
        },
        {
          id: this.$nano.id(),
          label: "Cancel",
          value: "Cancelled",
        },
      ],
      activeViewLoad: null,
      hideColumns: [],
