      documentRequired: false,
      hasSelectedProcess: 0,
      hasSelectedWorkflow: 0,
      taskFormId: 0,
      taskRepositoryId: 0,
      addTaskForm: {},
      addTaskSheet: false,
      taskEntryId: [],
